:root, :host {
    /**
     * Dependency terminal size
     */
    --b-dependency-terminal-size                     : 14px;

    /**
     * Dependency terminal offset from the event edge, negative values move away from the event
     */
    --b-dependency-terminal-offset                   : -4px;

    /**
     * Dependency terminal z-index
     */
    --b-dependency-terminal-z-index                  : calc(var(--b-event-resize-handle-zindex) + 1);

    /**
     * Dependency terminal border width
     */
    --b-dependency-terminal-border-width             : 1px;

    /**
     * Dependency terminal border style
     */
    --b-dependency-terminal-border-style             : solid;

    /**
     * Dependency terminal border color. By default it matches event border color to match its look
     */
    --b-dependency-terminal-border-color             : inherit;

    /**
     * Color for a valid dependency line during creation
     */
    --b-dependency-valid-color                       : var(--b-color-green);

    /**
     * Color for an invalid dependency line during creation
     */
    --b-dependency-invalid-color                     : var(--b-color-red);

    /**
     * Color used in the header of the dependency creation tooltip when the dependency is valid
     */
    --b-dependency-tooltip-valid-header-color        : var(--b-dependency-valid-color);

    /**
     * Background used in the header of the dependency creation tooltip when the dependency is valid
     */
    --b-dependency-tooltip-valid-header-background   : color-mix(in srgb, var(--b-dependency-tooltip-valid-header-color), var(--b-mix) 90%);

    /**
     * Color used in the header of the dependency creation tooltip when the dependency is invalid
     */
    --b-dependency-tooltip-invalid-header-color      : var(--b-dependency-invalid-color);

    /**
     * Background used in the header of the dependency creation tooltip when the dependency is invalid
     */
    --b-dependency-tooltip-invalid-header-background : color-mix(in srgb, var(--b-dependency-tooltip-invalid-header-color), var(--b-mix) 90%);
}

