.b-time-axis-group-summary {
    display        : flex;
    flex-direction : row;
    width          : 100%;
    height         : 100%;

    .b-time-axis-tick {
        display         : flex;
        flex-direction  : column;
        align-items     : stretch;
        justify-content : center;
    }

    .b-time-axis-summary-value {
        text-align : center;
    }
}
