/*                     width   : 0; */
/*                     opacity : 0; */
/*                 } */
/*             } */
/*         } */
/*     } */
/* } */

.b-resizing-event {
    cursor : ew-resize;

    .b-sch-time-axis-cell,
    .b-sch-event-wrap {
        /* prevent drag create */
        pointer-events : none;
    }
}

.b-sch-event-wrap-resizing {
    z-index : 2;
}

.b-sch-event-resize-tip {
    .b-tooltip-content > div {
        display        : flex;
        flex-direction : column;
        gap            : 0.5em;
    }

    .b-sch-tip-message:empty {
        display : none;
    }
}
