/* @after Scheduler/view/TimelineBase.css */

.b-timeline-histogram {
    .b-grid-row {
        --b-grid-row-zindex: calc(var(--b-column-lines-zindex) + 2);
    }

    .b-timeline-histogram-cell {
        padding                 : 0;
        border-inline-end-width : 0;
    }

    /* No focus outline for histogram cells */
    .b-timeline-sub-grid .b-grid-cell.b-focused::after {
        display : none;
    }
}

.b-timeline-histogram-histogram {
    padding          : 0;
    background-color : transparent;
}
