:root, :host {
    /**
     * Summary cell border color
     */
    --b-time-axis-footer-cell-border-color : var(--b-grid-cell-border-color);
}

.b-sch-summary-bar {
    padding         : 0;
    flex-direction  : row;

    /* Override .b-grid-footer's centering */
    justify-content : flex-start;

    /* Override the b-fill-last-column stretching */
    flex-grow       : unset !important;

    .b-time-axis-tick {
        display         : flex;
        flex-direction  : column;
        text-align      : center;
        overflow        : hidden;
        justify-content : center;
        align-items     : stretch;
    }
}

.b-sch-summary-bar:not(.b-sch-vertical) {
    .b-time-axis-tick {
        padding : .5em 0;

        &:not(:last-child) {
            border-inline-end : 1px solid var(--b-time-axis-footer-cell-border-color);
        }
    }
}

.b-sch-vertical .b-sch-summary-bar {
    flex-direction : column;
    align-items    : stretch;
    gap            : 0;
    /* background     : $grid-footer-background-color !important; */
    /* color          : $grid-footer-text-color; */

    .b-time-axis-tick {
        padding : 0 0.5em;

        /* &:not(:last-child) { */
        /*     border-bottom : 1px solid $timeaxis-cell-footer-border-color; */
        /* } */
    }
}

.b-time-axis-summary-tip {
    display        : flex;
    flex-direction : row;
    flex-wrap      : wrap;

    header {
        width         : 100%;
        text-align    : center;
        font-weight   : 700;
        margin-bottom : 1em;
    }

    label {
        flex : 1 1 50%;
    }

    .b-time-axis-summary-value {
        flex : 0 0 auto;
    }

}
