:root, :host {
    --b-tree-summary-zindex : 4;
}

.b-tree-summary {
    .b-tick-cell-canvas {
        z-index : var(--b-tree-summary-zindex);
    }
}

.b-sch-tick-summary-cell {
    display        : flex;
    position       : absolute;
    contain        : strict;
    pointer-events : all;

    .b-animating & {
        transition : top var(--b-grid-row-transition-duration);
    }
}

.b-tree-summary-cell {
    display         : flex;
    flex            : 1;
    justify-content : center;
    align-items     : center;
    font-weight     : 400;

    &:empty {
        visibility : hidden;
    }
}
