.b-simple-event-editor {
    /**
     * Background of the editor
     */
    --b-simple-event-edit-editor-background : var(--b-primary-100);
}

.b-simple-event-editor {
    border-radius                  : inherit;

    /* Must be above the canvas elements */
    z-index                        : 15;

    --b-widget-floating-box-shadow : none;

    &.b-target-has-border .b-field {
        --b-text-field-border-width : 0;
    }

    .b-field {
        --b-text-field-background : var(--b-simple-event-edit-editor-background);

        /* Make field fill entire editor, to not look weird in vertical */
        align-items               : stretch;
    }

    .b-field-inner {
        border-radius : inherit;
    }

    .b-sch-horizontal & .b-field {
        height : 100%;
    }
}
