.b-recurrence-editor-panel {
    --b-panel-padding : 0;

    /* .b-button-group-label { */
    /*     font-size : calc(var(--b-text-field-label-font-size, 1em) * var(--b-text-field-label-scale, 1)); */
    /* } */

    > .b-recurrence-editor-panel-body-wrap {
        overflow : visible;

        > .b-recurrence-editor-panel-content {
            overflow : visible;

            /* Keep checkbox and label together */
            [data-ref="monthDaysNumberRadioField"],
            [data-ref="monthDaysRadioField"],
            [data-ref="positionAndDayRadioField"] {
                &.b-has-label.b-field {
                    display : grid;
                }
            }
        }
    }

    [data-ref="positionsCombo"],
    [data-ref="daysCombo"] {
        grid-column : 2;
    }

    .b-button-group {
        gap                             : 1px;
        width                           : 100%;
        height                          : auto;
        overflow                        : hidden;

        --b-button-focus-outline-offset : -2px;

        .b-button {
            border        : none;
            min-height    : 2.5em;
            border-radius : 0;
        }

        &.b-recurrence-days-button-group .b-button {
            flex           : 1 1 100%;
            min-width      : auto;
            padding-inline : 0;
        }

        &.b-recurrence-months-button-group {
            display               : grid;
            grid-template-columns : repeat(4, 1fr);
        }

        &.b-recurrence-month-days-button-group {
            display               : grid;
            grid-template-columns : repeat(7, 1fr);

            --b-button-padding    : .25em;
        }
    }
}
