.b-sticky-events.b-sch-horizontal {
    .b-sch-event-wrap:where(:not(.b-disable-sticky,.b-milestone-wrap)),
    .b-sch-resource-time-range {
        --b-sch-event-content-width : auto;

        /* Use native stickiness */
        .b-sch-event-content {
            position           : sticky;
            inset-inline-start : var(--b-sch-event-padding-inline);
        }
    }

    /* Because we start pushing the content as soon as the outer bar's start */
    /* goes out of view, the content cannot be centered when using sticky events. */
    .b-sch-resource-time-range {
        justify-content : flex-start;

        > div {
            overflow : clip;
            width    : 100%;
        }
    }
}

/* FF stickyness is unreliable with contain */
.b-firefox.b-sticky-events.b-sch-horizontal .b-sch-foreground-canvas {
    contain : none
}

.b-sticky-events.b-sch-vertical {
    .b-sch-event-wrap:where(:not(.b-disable-sticky,.b-milestone-wrap)),
    .b-sch-resource-time-range {
        --b-sch-event-content-height : auto;

        .b-sch-event {
            align-items : flex-start;
        }
    }
}
