:root, :host {
    --b-top-canvas-zindex : 1000;
}

.b-timeline-base {
    &.b-animating .b-grid-row {
        transition : height 0.2s, translate 0.2s, inset 0.2s;
    }
}

.b-time-axis-cell {
    --b-grid-cell-padding-inline        : 0;
    --b-grid-cell-focused-outline-width : 0;
}

/* Used by Gantt too */
.b-sch-canvas {
    position       : absolute;
    inset          : 0;
    pointer-events : none;
    contain        : strict;
    overflow       : clip;
    height         : max(1px, 100%); /* For unknown reasons inset is not enough */

    width          : var(--bi-total-column-width); /* Var set from code */
}

/* Used by Gantt too */
.b-sch-foreground-canvas {
    z-index : var(--b-events-zindex);
}

.b-sch-top-canvas {
    z-index : var(--b-top-canvas-zindex);
}

.b-calculation-progress-wrap,
.b-timeline-loading-indicator-wrap {
    position   : absolute;
    bottom     : 0;
    display    : flex;
    width      : 100%;
    height     : 2px;
    z-index    : 100;
}

.b-timeline-loading-indicator-wrap {
    background : color-mix(in srgb, var(--b-color-green), transparent 80%);
}

.b-calculation-progress,
.b-timeline-loading-indicator {
    opacity    : 0.6;

    background : var(--b-color-green);
}

.b-timeline-loading-indicator {
    animation  : b-anim-line-loop 3s linear infinite;
}

@keyframes b-anim-line-loop {
    0% {
        width               : 0;
        margin-inline-start : 0;
    }
    50% {
        width               : 100%;
        margin-inline-start : 0;
    }
    100% {
        width               : 100%;
        margin-inline-start : 100%;
    }
}
