:root, :host {
    /**
     * Label font size
     */
    --b-event-label-font-size      : .9em;

    /**
     * Label font weight
     */
    --b-event-label-font-weight    : var(--b-sch-event-font-weight);

    /**
     * Gap between label and event bar
     */
    --b-event-label-gap            : 0.5em;

    /**
     * Label color
     */
    --b-event-label-color          : var(--b-sch-event-color);

    /**
     * Label color when event is selected
     * @state Selected
     */
    --b-selected-event-label-color : var(--b-sch-event-color);

    /**
     * Label background
     */
    --b-event-label-background     : null;
}

.b-sch-event-wrap, .b-gantt-task-wrap {
    .b-sch-label {
        text-align          : center;
        white-space         : nowrap;
        z-index             : 1; /* Above dependency terminal hover area */
        min-width           : 1em; /* To guarantee Siesta can reach it */
        pointer-events      : all; /* .b-gantt-task-wrap has pointer-events: none for some reason */
        user-select         : none;
        -webkit-user-select : none;

        background          : var(--b-event-label-background);
        color               : var(--b-event-label-color);
        font-size           : var(--b-event-label-font-size);
        font-weight         : var(--b-event-label-font-weight);
    }

    &.b-sch-event-selected .b-sch-label {
        --b-event-label-color : var(--b-selected-event-label-color);
    }

    .b-sch-label-top {
        grid-area     : top;
        justify-self  : center;
        margin-bottom : .2em;
    }

    .b-sch-label-bottom {
        grid-area    : bottom;
        justify-self : center;
        margin-top   : .2em;
    }

    .b-sch-label-before {
        grid-area         : before;
        align-self        : center;
        justify-self      : end;
        margin-inline-end : var(--b-event-label-gap);
    }

    .b-sch-label-after {
        grid-area           : after;
        align-self          : center;
        justify-self        : start;
        margin-inline-start : var(--b-event-label-gap);
    }
}

/* The measuring element for labels */
.b-sch-event-wrap.b-measure-label {
    height             : 1em;
    position           : fixed;
    top                : -10000px;
    inset-inline-start : -10000px;

    visibility         : hidden;

    label {
        padding-inline-start : var(--b-event-label-gap);
        grid-area            : none;
    }
}

.b-sch-label-editor {
    background : var(--b-neutral-100);
}
