:root, :host {
    /**
     * Font size for badge showing event count
     */
    --b-date-picker-cell-badge-font-size   : 0.7em;

    /**
     * Background color for badge showing event count
     */
    --b-date-picker-cell-badge-background  : var(--b-color-red);

    /**
     * Text color for badge showing event count
     */
    --b-date-picker-cell-badge-color       : #fff;

    /**
     * Size of badge showing event count
     */
    --b-date-picker-cell-badge-size        : 1.7em;

    /**
     * Heatmap color for 1 to 3 events in the date picker.
     */
    --b-date-picker-1-to-3-events-color    : var(--b-color-yellow);

    /**
     * Heatmap color for 4 to 6 events in the date picker.
     */
    --b-date-picker-4-to-6-events-color    : var(--b-color-orange);

    /**
     * Heatmap color for 7 or more events in the date picker.
     */
    --b-date-picker-7-or-more-events-color : var(--b-color-red);
}

.b-date-picker {
    .b-cell-events-badge {
        display       : grid;
        place-items   : center;
        border-radius : 50%;
        aspect-ratio  : 1 / 1;
        inset         : auto auto -2px auto;

        font-size     : var(--b-date-picker-cell-badge-font-size);
        background    : var(--b-date-picker-cell-badge-background);
        color         : var(--b-date-picker-cell-badge-color);
        width         : var(--b-date-picker-cell-badge-size);
    }

    .b-calendar-panel-cell {
        &.b-date-picker-1-to-3-events:hover,
        &.b-date-picker-4-to-6-events:hover,
        &.b-date-picker-7-or-more-events:hover {
            border-radius : 0;

            .b-calendar-cell-inner {
                background-color : var(--b-year-view-heatmap-hover-background, var(--b-primary-75));
            }
        }

        &.b-has-bullet {
            /*
             * When sharing the vertical space with an event bullet
             * we need to reduce the padding on top a bit to keep things centered
             */
            .b-date-picker-cell-inner {
                justify-content : flex-start;
                padding-top     : 2px;
            }
            .b-date-picker-cell-payload {
                bottom : 2px;
            }
        }
        &.b-has-badge {
            /*
             * When sharing the vertical space with an event count badge
             * we need to reduce the padding on top a bit to keep things centered
             */
            .b-date-picker-cell-inner {
                justify-content : flex-start;
                padding-top     : 2px;
            }
        }
    }

    .b-show-events-heatmap {
        .b-calendar-panel-cell:not(:hover) {
            /**
            * Heatmap background (a shade of the heatmaps color)
            */
            --b-date-picker-heatmap-background : var(--b-neutral-95);

            /* .b-date-picker-1-to-3-events,
            .b-date-picker-4-to-6-events,
            .b-date-picker-7-or-more-events {
                background : var(--b-date-picker-heatmap-background);
            } */

            &.b-date-picker-1-to-3-events .b-date-picker-cell-inner {
                background : color-mix(in srgb, var(--b-date-picker-1-to-3-events-color), var(--b-date-picker-heatmap-background));
            }

            &.b-date-picker-4-to-6-events .b-date-picker-cell-inner {
                background : color-mix(in srgb, var(--b-date-picker-4-to-6-events-color), var(--b-date-picker-heatmap-background));
            }

            &.b-date-picker-7-or-more-events .b-date-picker-cell-inner {
                background : color-mix(in srgb, var(--b-date-picker-7-or-more-events-color), var(--b-date-picker-heatmap-background));
            }
        }
    }

    .b-show-events-dots {
        .b-date-picker-cell-payload {
            top      : 1.7em;
            overflow : visible;
        }
    }
}
