     */
    /*--b-time-selection-background        : var(--b-primary-85);*/
/* } */

.b-sch-time-axis-column .b-selected-time-span,
.b-vertical-time-axis-column .b-selected-time-span {
    color           : #fff;
    justify-content : space-between;
    padding         : 0.4em;
    opacity         : 1;
    line-height     : 1em;

    background      : var(--b-time-selection-header-background, var(--b-primary-60));

    span {
        white-space : nowrap;
        overflow    : hidden;
    }

    .b-selection-start,
    .b-selection-end {
        align-self : flex-end;
    }

    .b-icon-close {
        position         : absolute;
        top              : 0.2em;
        inset-inline-end : 0.4em;
        cursor           : pointer;
    }
}

/* Horizontal styles */
.b-sch-time-axis-column .b-selected-time-span {
    font-size : 0.7em;
}

/* Vertical adaptions */
.b-vertical-time-axis-column .b-selected-time-span {
    width : calc(100% - 2em);

    span {
        font-size : 0.7em;
    }

    .b-icon-close {
        inset-inline-start : 0.4em;
    }
}

.b-grid-header,
.b-timeline-sub-grid,
.b-vertical-time-axis-column {
    .b-selected-time-span {
        transition : none;
        z-index    : 10; /* Float above events*/
    }
}

.b-timeline-sub-grid .b-selected-time-span {
    --b-time-range-background : var(--b-time-selection-background, var(--b-primary-85));
    --b-time-range-opacity    : var(--b-time-selection-opacity);
}


